'use client';

import { FaDiscord } from 'react-icons/fa';
import { FiUpload, FiSearch, FiUser } from 'react-icons/fi';

export default function Navbar() {
  return (
    <nav className="flex items-center justify-between px-6 py-4 bg-black text-white">
      {/* Left: Logo */}
      <div className="flex items-center gap-2 text-white text-2xl font-bold">
        <span className="text-[#7F00FF]">3dimli</span>
        <span className="text-xs font-normal ml-1">BETA 1.0.1</span>
        <span className="mx-4 h-6 w-px bg-gray-700" />
      </div>

      {/* Middle: Nav links */}
      <ul className="flex gap-6 font-medium text-sm">
        <li className="cursor-pointer hover:text-gray-300">Home</li>
        <li className="cursor-pointer hover:text-gray-300">Discover</li>
        <li className="cursor-pointer hover:text-gray-300">Features</li>
        <li className="cursor-pointer hover:text-gray-300">Pricing</li>
      </ul>

      {/* Search bar */}
      <div className="flex items-center bg-[#1a1a1a] px-4 py-2 rounded-full ml-6 w-[300px]">
        <input
          type="text"
          placeholder="Search..."
          className="bg-transparent outline-none text-sm w-full placeholder-gray-400 text-white"
        />
        <FiSearch className="text-gray-400 ml-2" />
      </div>

      {/* Right: Buttons */}
      <div className="flex gap-3 items-center ml-6">
        <button className="flex items-center gap-2 bg-[#0a1a4f] px-4 py-2 rounded-full text-sm">
          <FaDiscord />
          Discord
        </button>
        <button className="flex items-center gap-2 bg-[#0b2a74] px-4 py-2 rounded-full text-sm">
          <FiUpload />
          Upload
        </button>
        <div className="w-9 h-9 rounded-full border border-gray-600 flex items-center justify-center">
          <FiUser />
        </div>
      </div>
    </nav>
  );
}
